.login{
    display:flex;
    flex-direction:column;
    align-items:center;
    background-color:white;
    height:100vh;
}
.login__logo{
    margin-top:20px;
    margin-bottom:20px;
    object-fit:contain;
    width:100px;
    margin-right:auto;
    margin-left:auto;
}
.login__container{
    width:300px;
    height:fit-content;
    display:flex;
    flex-direction: column;
    border-radius:5px;
    border:1px solid lightyellow;
    padding:20px;
}
.login__container > h1{
    margin-bottom:20px;
    font-weight:500;
}
.login__container > form >h5{
    margin-bottom:5px;
}
.login__container >form > input{
    margin-bottom:10px;
    height:30px;
    background-color: white;
    width:98%;
    border:0.5px solid lightgray;
}
.login__container > p{
    margin-top:15px;
    font-size:12px;
}
.login__container > h6{
    margin-left:100px;
    margin-top:10px;
    margin-bottom:10px;
}
.login__signin{
    background: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    cursor: pointer;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.login__register{
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: lightgray;
    background-color: white;
    cursor:pointer;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.login__register:hover{
    transform: scale(1.05);
    border:2px solid;
    border-color:#f0c14b;
}
.login__signin:hover{
       transform:scale(1.05);
    
}